.modal-movie__backdrop {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  transition: transform 1000ms, cubic-bezier(0.175, 0.885, 0.32, 1.275),
    opacity 1000ms cubic-bezier(0.175, 0.885, 0.32, 1.275),
    visibility 1000ms cubic-bezier(0.23, 1, 0.32, 1);
}

.modal-movie__backdrop--is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-movie {
  height: 568px;
  width: 882px;
  position: absolute;
  left: 71px;
  top: 100px;
  background-color: getColor('background-modal');

  @media screen and (min-width: 768px) {
    height: 562px;
    width: 618px;
    left: 75px;
    top: 231px;
  }

  @media screen and (min-width: 320px) {
    height: 958px;
    width: 280px;
    left: 20px;
    top: 20px;
  }
}
