// Header

.header {
  padding: 40px 0;
  height: 230px;
  color: getColor(header-text-color);

  background-color: getColor(header-background-color);
  background-image: url('../images/header-bg.png');
  background-size: cover;
  background-repeat: none;

  box-shadow: 0 2px 12px 2px rgba(0, 0, 0, 0.5);
}

// navigation

.nav {
  margin-bottom: 60px;
  height: 24px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.logo__icon {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.logo__tittle {
  font-size: 30px;
  font-weight: 500;
}

.nav__list {
  display: flex;
  text-transform: uppercase;
}

.nav__item {
  font-weight: 500;

  &:not(:last-child) {
    margin-right: 40px;
  }
}

// form

.form {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  max-width: 336px;
}

.form--wrapper {
  position: relative;
  width: 100%;
}

.form__input {
  width: 100%;
  font-size: 14px;
  color: getColor(header-text-color);
  background-color: transparent;
  border: none;
  outline: none;
  border-bottom: 1px solid getColor(header-text-color);
}

.form__btn {
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  padding: 0px;

  background-color: transparent;
  border: none;
  cursor: pointer;
}

.form__icon {
  width: 100%;
  height: 100%;
}

.notification {
  margin: 0 auto;
  font-size: 14px;
  color: getColor(error);

  display: flex;
  justify-content: center;

  opacity: 1;
  visibility: visible;
}

.off {
  opacity: 0;
  visibility: hidden;
}
